import React from "react";
import ScrollAnimation from "react-animate-on-scroll";

import { Link } from "react-router-dom";
import DarkWaves from "../Layout/DarkWaves";

// array from objects with names: Athletism, Futsal, Basket, Swimming, Volleyball, Golf, Tennis
export const sports = [
  {
    name: "Athletism",
    icon: "running",
    image:
      "https://images.unsplash.com/photo-1452626038306-9aae5e071dd3?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=753&q=80",
  },
  {
    name: "Futsal",
    icon: "football",
    image:
      "https://images.unsplash.com/photo-1587384474964-3a06ce1ce699?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=750&q=80"    
  },
  {
    name: "Basketball",
    icon: "basketball",
    image:
      "https://images.unsplash.com/photo-1519861531473-9200262188bf?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=751&q=80",
  },
  {
    name: "Swimming",
    icon: "swimmer",
    image:
      "https://images.unsplash.com/photo-1530549387789-4c1017266635?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=750&q=80",
  },
  {
    name: "Volleyball",
    icon: "volleyball",
    image:
      "https://images.unsplash.com/photo-1612872087720-bb876e2e67d1?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=868&q=80",
  },
  {
    name: "Golf",
    icon: "golf",
    image:
      "https://images.unsplash.com/photo-1576220258822-153014832245?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=764&q=80",
  },
  {
    name: "Tennis",
    icon: "tennis",
    image:
      "https://images.unsplash.com/photo-1587280501635-68a0e82cd5ff?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=750&q=80",
  },
  {
    name: "Ping Pong",
    icon: "pingPong",
    image:
      "https://www.buenosaires.gob.ar/sites/gcaba/files/field/image/_mg_8081_0.jpg",
  }
];

const Sports = () => {
  const showSports = (sports = []) =>
    sports.map((sport, i) => {
      const Icon = require(`../../assets/svgs/sports/${sport.icon}.svg`);
      console.log(Icon.default);
      return (
        <div
          className="col-lg-3 col-md-4 col-sm-6 col-12 overflow-hidden"
          key={i}
        >
          <ScrollAnimation animateIn={"fadeInUp"} animateOnce>
            <div className="shadow p-3 my-3">
              <div className="row">
                <div className="col-4 d-flex justify-content-center align-items-center">
                  <img
                    src={Icon.default}
                    className="img-fluid aspect"
                    height="auto"
                    alt={sport.name}
                  />
                </div>
                <div className="col-8">
                  <h3 className="righteous">{sport.name}</h3>
                  <Link
                    to={`/sports/${encodeURIComponent(sport.name)}`}
                    className="btn btn-outline-dark rounded-0"
                  >
                    View More
                  </Link>
                </div>
              </div>
            </div>
          </ScrollAnimation>
        </div>
      );
    });

  return (
    <section
      id="sports"
      className="bg-white overflow-hidden position-relative text-dark py-5"
    >
      <div className="container pb-5 mb-5">
        <h1 className="righteous display-4 text-center">Deportes</h1>
        <div className="row md-margin-bottom">{showSports(sports)}</div>
      </div>      
      <DarkWaves/>
    </section>
  );
};

export default Sports;
