import React from "react";

import logoApn from "../../assets/logos/institutions/logo-apn.png";
import logoBA from "../../assets/logos/institutions/logo-ciudad-de-ba.png";
import logoMTD from "../../assets/logos/institutions/logo-min-turismo-deportes.png"

const Institutions = () => {
    return (
        <article className="md-margin-bottom">
            <h1 className="righteous display-8 text-center">
                Con el auspicio de
            </h1>
            <div className="row">
                <div className="col-lg-12">
                    <div className="row justify-content-center align-items-center">
                        <div className="col-2 mx-auto px-md-5 py-2">
                            <img
                                className="w-100 aspect rounded-pill shadow"
                                src={logoApn}
                                alt="Logo"
                            />
                        </div>
                        <div className="col-3 mx-auto px-md-5 py-3">
                            <img
                                className="w-100 "
                                src={logoBA}
                                alt="Logo"
                            />
                        </div>
                        <div className="col-3 mx-auto px-md-5 py-3">
                            <img
                                className="w-100 "
                                src={logoMTD}
                                alt="Logo"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </article>
    );
}

export default Institutions;