import React from "react";
import AboutConfra from "../About/AboutConfra";
import AboutCNA from "../About/AboutCNA";
import Institutions from "../About/Institutions"
import WhiteWaves from "../Layout/WhiteWaves";

const About = () => {
  return (
    <section className="about position-relative overflow-hidden" id="about">
      <div className="container pb-5 mb-5">
        <AboutConfra />
        <AboutCNA />
        <Institutions />
        <WhiteWaves />
      </div>
    </section>
  );
};

export default About;
