import React from "react";
import logo1 from "../../assets/logos/sponsors/logo1.jpg";
import logo2 from "../../assets/logos/sponsors/logo2.jpg";
import logo3 from "../../assets/logos/sponsors/logo3.jpg";
import logo4 from "../../assets/logos/sponsors/logo4.jpg";
import DarkWaves from "../Layout/DarkWaves";

const Sponsors = () => {
  return (
    <section
      id="sponsors"
      className="py-5 bg-white position-relative text-dark"
    >
      <div className="container">
        <h1 className="righteous text-dark display-4 text-center pb-4">
          Sponsors
        </h1>
        <div className="row">
          <div className="col-lg-6 d-flex justify-content-center align-items-center">
            <div className="text-center">
              <h1 className="righteous position-relative d-inline-block mx-auto section-title display-6 text-center pb-2 my-3">
                Main Sponsor
              </h1>
            </div>
          </div>
          <div className="col-lg-6">
            <div className="row">
              <div className="col-6 mx-auto px-md-5 py-3">
                <img
                  className="w-100 aspect rounded-pill shadow"
                  src={logo1}
                  alt="Logo"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <hr />
      <div className="container">
        <div className="row">
          <div className="col-lg-6 d-flex justify-content-center align-items-center">
            <div className="text-center">
              <h1 className="righteous position-relative d-inline-block mx-auto section-title display-6 text-center pb-2 my-3">
                Platinum Sponsors
              </h1>
            </div>
          </div>
          <div className="col-lg-6">
            <div className="row">
              <div className="col-5 mx-auto px-md-5 py-3">
                <img
                  className="w-100 aspect rounded-pill shadow"
                  src={logo2}
                  alt="Logo"
                />
              </div>
              <div className="col-5 mx-auto px-md-5 py-3">
                <img
                  className="w-100 aspect rounded-pill shadow"
                  src={logo3}
                  alt="Logo"
                />
              </div>
              <div className="col-5 mx-auto px-md-5 py-3">
                <img
                  className="w-100 aspect rounded-pill shadow"
                  src={logo4}
                  alt="Logo"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <hr />
      <div className="container pb-5 mb-5">
        <div className="row">
          <div className="col-lg-6 d-flex justify-content-center align-items-center">
            <div className="text-center">
              <h1 className="righteous position-relative d-inline-block mx-auto section-title display-6 text-center pb-2 my-3">
                Gold Sponsors
              </h1>
            </div>
          </div>
          <div className="col-lg-6">
            <div className="row">
              <div className="col-4 px-md-4 py-3">
                <img
                  className="w-100 aspect rounded-pill shadow"
                  src={logo1}
                  alt="Logo"
                />
              </div>
              <div className="col-4 px-md-4 py-3">
                <img
                  className="w-100 aspect rounded-pill shadow"
                  src={logo2}
                  alt="Logo"
                />
              </div>
              <div className="col-4 px-md-4 py-3">
                <img
                  className="w-100 aspect rounded-pill shadow"
                  src={logo3}
                  alt="Logo"
                />
              </div>
              <div className="col-4 px-md-4 py-3">
                <img
                  className="w-100 aspect rounded-pill shadow"
                  src={logo4}
                  alt="Logo"
                />
              </div>
              <div className="col-4 px-md-4 py-3">
                <img
                  className="w-100 aspect rounded-pill shadow"
                  src={logo1}
                  alt="Logo"
                />
              </div>
              <div className="col-4 px-md-4 py-3">
                <img
                  className="w-100 aspect rounded-pill shadow"
                  src={logo2}
                  alt="Logo"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <DarkWaves/>
    </section>
  );
};

export default Sponsors;
