import React, { useEffect, useState } from "react";
import logo from "../../assets/logos/logo-confra-horizontal.png";
import { ReactComponent as Youtube } from "../../assets/svgs/socialMedia/youtube.svg";
import { ReactComponent as Facebook } from "../../assets/svgs/socialMedia/facebook.svg";
import { ReactComponent as Instagram } from "../../assets/svgs/socialMedia/instagram.svg";
const Header = () => {
  const [headerActiveClass, setHeaderActive] = useState("");
  useEffect(() => {
    const chk = () =>
      window.scrollY > 100
        ? setHeaderActive(" nav-sticky")
        : setHeaderActive("");
    chk();
    window.addEventListener("scroll", chk);
    return () => {
      window.removeEventListener("scroll", chk);
    };
  }, []);
  return (
    <nav
      className={`navbar navbar-expand-lg navbar-dark fixed-top navbar-custom sticky sticky-dark${headerActiveClass}`}
    >
      <div className="container px-2">
        {/* LOGO */}
        <a
          className="logo text-decoration-none animate__animated d-flex align-items-center animate__tada"
          href="/"
        >
          <img src={logo} width={150} alt="CONFRA" />
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarTogglerDemo03"
          aria-controls="navbarTogglerDemo03"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon" />
        </button>
        <div
          className="collapse navbar-collapse justify-content-between"
          id="navbarTogglerDemo03"
        >
          <div></div>
          <ul className="navbar-nav navbar-center" id="mySidenav">
            <li className="nav-item active">
              <a href="#home" className="nav-link">
                Home
              </a>
            </li>
            <li className="nav-item">
              <a href="#about" className="nav-link">
                About
              </a>
            </li>
            <li className="nav-item">
              <a href="#sports" className="nav-link">
                Sports
              </a>
            </li>
            <li className="nav-item">
              <a href="#location" className="nav-link">
                Location
              </a>
            </li>
            <li className="nav-item">
              <a href="#sponsors" className="nav-link">
                Sponsors
              </a>
            </li>
          </ul>
          <div className="contact">
            <a href="/" className="btn">
              <Facebook width={20} fill="#fff" className="shadow" height={20} />
            </a>
            <a href="/" className="btn">
              <Instagram
                width={20}
                fill="#fff"
                className="shadow"
                height={20}
              />
            </a>
            <a href="/" className="btn">
              <Youtube width={20} fill="#fff" className="shadow" height={20} />
            </a>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Header;
