import React from "react";
import logo from "../../assets/logos/logo-confra-horizontal.png";
// import youtube , facebook, instagram
import { ReactComponent as Youtube } from "../../assets/svgs/socialMedia/youtube.svg";
import { ReactComponent as Facebook } from "../../assets/svgs/socialMedia/facebook.svg";
import { ReactComponent as Instagram } from "../../assets/svgs/socialMedia/instagram.svg";

const Footer = () => {
  return (
    <footer className="text-white py-5">
      <div className="container py-4">
        <div className="row">
          <div className="col-lg-12">
            <div className="row">
              <div className="col-lg-2 px-0">
                <img src={logo} width={200} className="mw-100" alt="CONFRA" />
              </div>
            </div>
          </div>
          <div className="w-100 my-4" />
          <div className="col-lg-2">
            <p className="text-white-50 fw-bold" style={{ fontSize: "0.7rem" }}>
              © {new Date().getFullYear()} CONFRA. <br /> All Rights Reserved
            </p>
          </div>
          <div className="col-lg-9">
            <div className="row">
              <div className="col-lg-12">
                <h4 className="fw-bold">Un equipo, un corazón</h4>
              </div>
              <div className="w-100 my-4" />
              <div className="col-lg-4 col-6 my-4">
                <h6 className="text-white-50 fw-bold">Join the Event</h6>
                <div>
                  <a
                    href="/"
                    className="fw-bold text-white footer_link header_link active d-inline-block"
                    style={{ fontSize: "0.9rem" }}
                  >
                    info@confra.ar
                  </a>
                </div>
                <div>
                  <a
                    href="/"
                    className="fw-bold text-white footer_link header_link active d-inline-block"
                    style={{ fontSize: "0.9rem" }}
                  >
                    +1 233 456 0789
                  </a>
                </div>
              </div>
              <div className="col-lg-4 col-6 my-4">
                <h6 className="text-white-50 fw-bold">For More Information</h6>
                <div>
                  <a
                    href="#location"
                    className="fw-bold text-white footer_link header_link active d-inline-block"
                    style={{ fontSize: "0.9rem" }}
                  >
                    Location
                  </a>
                </div>
                <div>
                  <a
                    href="/"
                    className="fw-bold text-white footer_link header_link active d-inline-block"
                    style={{ fontSize: "0.9rem" }}
                  >
                    2023 ARG
                  </a>
                </div>
              </div>
              <div className="col-lg-4 col-12">
                <div className="row text-white-50">
                  <div className="col-12">
                    <h6 className="fw-bold mb-4">Contact Us</h6>
                  </div>
                  <div className="w-100" />
                  <div className="col">
                    <Youtube width={20} height={20} fill="#fff" />
                  </div>
                  <div className="col">
                    <Facebook width={20} height={20} fill="#fff" />
                  </div>
                  <div className="col">
                    <Instagram width={20} height={20} fill="#fff" />
                  </div>
                  <div className="col"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
